(function() {
  angular.module('homemademessClient', ['ngCookies', 'ngSanitize', 'ngAria', 'ngResource', 'ui.router', 'akoenig.deckgrid', 'ngMaterial', 'ngDropzone', 'ngTagsInput', 'ngLodash']).filter('taglink', function() {
    return function(input) {
      return input.replace(/\s/g, '_');
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('slideshow', function() {
    var directive, slideshowCtrl;
    slideshowCtrl = [
      '$scope', '$mdDialog', '$stateParams', '$state', '$document', function($scope, $mdDialog, $stateParams, $state, $document) {
        $scope.open = false;
        $scope.transition = false;
        $document.bind('keyup', function(e) {
          var slide;
          if ($stateParams.slide && $scope.open && !$scope.transition) {
            slide = Number($stateParams.slide);
            switch (e.which) {
              case 39:
                if ($scope.slides.length > (slide + 1)) {
                  return $state.go($state.current, {
                    slide: slide + 1
                  });
                }
                break;
              case 37:
                if ((slide - 1) > -1) {
                  return $state.go($state.current, {
                    slide: slide - 1
                  });
                }
                break;
              case 38:
                return $state.go($state.current, {
                  slide: 0
                });
              case 40:
                return $state.go($state.current, {
                  slide: $scope.slides.length - 1
                });
            }
          }
        });
        $scope.$on('$locationChangeSuccess', function(e) {
          return $scope.updateShowState();
        });
        $scope.$watch('slides', function(nw, old) {
          if (nw && !old && !$scope.open) {
            return $scope.updateShowState();
          }
        });
        $scope.updateShowState = function() {
          if (!$scope.open && $stateParams.slide && $scope.slides[$stateParams.slide]) {
            $scope.open = true;
            return $scope.showSlide($stateParams.slide);
          }
        };
        return $scope.showSlide = function(i) {
          i = Number(i);
          return $mdDialog.show({
            clickOutsideToClose: true,
            locals: {
              i: i,
              img: $scope.slides[i].derivative[2].uri,
              label: $scope.slides[i].filename
            },
            scope: $scope,
            preserveScope: true,
            onRemoving: function() {
              return $scope.open = false;
            },
            template: '<md-dialog aria-label="{{label}}" class="slide" ng-class="{loading: transition}">' + '  <md-dialog-content>' + '     <img ng-src="{{img}}" class="img-responsive" ng-class="imgclasses" />' + '     <div class="loading-spinner"></div>' + '  </md-dialog-content>' + '</md-dialog>',
            controller: function($scope, $mdDialog, $stateParams, i, img, label) {
              $scope.i = i;
              $scope.img = img;
              $scope.label = label;
              $scope.imgclasses = {};
              $scope.$on('$locationChangeSuccess', function(e) {
                if ($stateParams.slide) {
                  return $scope.update($stateParams.slide);
                }
              });
              $scope.update = function(i) {
                var loader, src;
                $scope.transition = true;
                src = $scope.slides[i].derivative[2].uri;
                loader = new Image();
                loader.onload = function() {
                  $scope.imgclasses = $scope.toggleClass(Number(i));
                  $scope.i = Number(i);
                  $scope.img = src;
                  $scope.label = $scope.slides[i].filename;
                  $scope.transition = false;
                  return $scope.$digest();
                };
                return loader.src = src;
              };
              return $scope.toggleClass = function(i) {
                var classes;
                classes = {
                  up: false,
                  down: false,
                  left: false,
                  leftagain: false,
                  right: false,
                  rightagain: false
                };
                switch (false) {
                  case !(i < $scope.i && (($scope.i - i) > 1) && i === 0):
                    classes.up = true;
                    break;
                  case !(i > $scope.i && ((i - $scope.i) > 1) && i === ($scope.slides.length - 1)):
                    classes.down = true;
                    break;
                  case !(i < $scope.i && ($scope.i - i) === 1):
                    if ($scope.imgclasses.left) {
                      classes.leftagain = true;
                    } else {
                      classes.left = true;
                    }
                    break;
                  case !(i > $scope.i && ((i - $scope.i) === 1)):
                    if ($scope.imgclasses.right) {
                      classes.rightagain = true;
                    } else {
                      classes.right = true;
                    }
                }
                return classes;
              };
            }
          }).then(null, function() {
            return $state.go($state.$current.parent);
          });
        };
      }
    ];
    return directive = {
      restrict: 'E',
      controller: slideshowCtrl,
      scope: {
        slides: '='
      }
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('searchbar', function() {
    var SearchCtrl, directive;
    SearchCtrl = [
      '$scope', '$resource', 'lodash', '$q', '$location', 'apiUrl', function($scope, $resource, lodash, $q, $location, apiUrl) {
        var Auto, Images;
        $scope.tags = [];
        $scope.isActive = function(route) {
          return route === $location.path();
        };
        $scope.mapTags = function(tags) {
          return lodash.uniq(lodash.map(tags, '_id'));
        };
        Images = $resource(apiUrl + '/images');
        $scope.redoSearch = function() {
          return Images.get({
            tags: $scope.mapTags($scope.tags)
          }, function(result) {
            return $scope.view.images = result.images;
          });
        };
        Auto = $resource(apiUrl + '/auto');
        return $scope.findTags = function(value) {
          return Auto.query({
            q: value
          }).$promise;
        };
      }
    ];
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/searchbar/searchbar.html',
      controller: SearchCtrl
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').controller('MainCtrl', function($scope, $http, $stateParams, apiUrl, $state, $document) {
    var getURI;
    $scope.view = {};
    if ($stateParams.tag) {
      getURI = apiUrl + '/tagged/' + $stateParams.tag;
      $scope.containerClass = 'tagged';
      $scope.tag = $stateParams.tag.replace(/_/g, ' ');
    } else {
      getURI = apiUrl + '/images';
      $scope.containerClass = 'main';
    }
    $http.get(getURI).success(function(result) {
      $scope.view.images = result.images;
      $scope.view.tags = result.tags;
      return $scope.view.offset = 0;
    });
    return $scope.aspect = function(img, n) {
      var a, aspect, columns, rows;
      a = Math.round((img.width / img.height) * 100) / 100;
      if (a < 1) {
        columns = (function() {
          switch (false) {
            case !(a < 0.5):
              return 8;
            case !(a < 0.25):
              return 6;
            case !(a < 0.25):
              return 4;
            default:
              return 12;
          }
        })();
        rows = Math.round(columns * img.height / img.width);
      } else {
        rows = (function() {
          switch (false) {
            case !(a > 1.51):
              return 12;
            case !(a > 2):
              return 16;
            case !(a > 3):
              return 24;
            default:
              return 8;
          }
        })();
        columns = Math.round(rows * a);
      }
      aspect = [columns, rows];
      return aspect[n];
    };
  });

}).call(this);


